.edit_container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f0f2f5;
  .header {
    background-color: #fff;
    height: 40px;
    padding: 5px 12px;
  }
  .content_wrapper {
    flex: auto;
    padding: 12px 0;
    .content {
      margin: 0 24px;
      display: flex;
      height: 100%;
      height: calc(100vh - 75px);
    }
    .left {
      width: 285px;
      background-color: #fff;
      padding: 0 12px;
      overflow: auto;
    }
    .main {
      flex: 1;
      position: relative;
      overflow: hidden;
      .canvas_wrapper {
        position: absolute;
        width: 400px;
        height: 712px;
        top: 50%;
        left: 50%;
        background-color: #fff;
        transform: translateX(-50%) translateY(-50%);
        overflow: auto;
        box-shadow: 0 2px 10px #0000001f;
      }
    }
    .right {
      width: 300px;
      background-color: #fff;
      padding: 0 12px;
    }
  }
}
