.Edit_header_wrapper {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
 
  .header {
    display: flex;
    margin: 0 auto;
    h1 {
      font-size: 18px;
      margin: 0;
    }
    .left {
      flex: 1;
    }
    .main {
      flex: 1;
      text-align: center;
    }
    .right {
      flex: 1;
      text-align: right;
    }
  }
}
