.edit_canvas {
  min-height: 300px;
  background-color: #fff;
  overflow: auto;
  .selected {
    border-color: #1890ff !important;
  }
  .locked {
    opacity: 0.5;
    cursor: not-allowed;
  }
  .component_wrapper {
    margin: 12px;
    border: 1px solid #fff;
    padding: 12px;
    border-radius: 3px;
    &:hover {
      border-color: #d9d9d9;
    }
    .component {
      pointer-events: none;
    }
  }
}
