.componentLib_wrapper {
  margin-bottom: 12px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fff;
  padding: 12px;
  border-radius: 3px;
  &:hover {
    border-color: #d9d9d9;
  }
  .component {
    pointer-events: none;
  }
  .libComStyle {
    display: flex;
    justify-content: space-between;
    border: 1px solid;
    padding: 5px;
    border-radius: 5px;
    margin: 3px 0;
  }
}
